import { Component, OnInit } from '@angular/core';
import { MenuController, ModalController } from '@ionic/angular';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { MessageService } from 'primeng/api';
import { DialogService, DynamicDialogRef } from 'primeng/dynamicdialog';

import { setupWebSockets } from './shared/webSocket';
import { PreferencesService } from './shared/services/preferences/preferences.service';
import { ThemeService } from './shared/services/theme/theme.service';
import { AuthService } from './shared/services/auth/auth.service';
import { LoaderService } from './shared/services/app-loader/loader.service';
import { TransacaoService } from './shared/services/transacao/transacao.service';
import { StorageService } from './shared/services/auth/storage.service';
import { StatusTransacaoEnum } from './shared/enumerations/statusTransacao.enum';
import { TransacaoSocketService } from './shared/webSocket/transacao/transacao-socket.service';
import { NovaTransacaoModalComponent } from './shared/components/fluxo-module/nova-transacao-modal/nova-transacao-modal.component';
import { Toast } from './shared/enumerations/toast.enum';
import { AnimationBuilder } from '@angular/animations';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent implements OnInit {
  isDarkTheme = false;

  transacoesPendentes = 0;

  obs = new Subject();

  ref: DynamicDialogRef;

  blockUI = false;


  public appPages = [
    { title: 'Fluxo de Transações', url: '/home', icon: 'cash' },
    { title: 'Consultar Transações', url: '/transacoes', icon: 'list' },
    { title: 'ADM. Financeira', url: '/administracao-financeira', icon: 'wallet' },
    { title: 'ADM. Recurso', url: '/administracao-recurso', icon: 'laptop' },
    { title: 'ADM. Geral', url: '/administracao-geral', icon: 'business' },
    { title: 'Perfil', url: '/perfil', icon: 'person' },
  ];

  constructor(
    private preferencesService: PreferencesService,
    private themeService: ThemeService,
    private auth: AuthService,
    public loaderService: LoaderService,
    private transacaoService: TransacaoService,
    private storage: StorageService,
    private transacaoSocketService: TransacaoSocketService,
    public dialogService: DialogService,
    private menu: MenuController,
    private toastService: MessageService,
    private modal: ModalController,
  ) {
    const theme = this.preferencesService.getThemePreference();
    document.body.setAttribute('color-theme', theme);

    if (theme === 'dark') {
      this.isDarkTheme = true;
      this.themeService.switchTheme('bootstrap4-dark-yellow');
    } else {
      this.isDarkTheme = false;
      this.themeService.switchTheme('saga-blue');
    }
  }

  ngOnInit() {
    setupWebSockets();
   // this.presentTransacaoRequest();
    this.showModal();
  }

  ionWillOpen() {
    //TODO Adicionar recurso que retorne apenas a quantidade de transacoes PENDENTES (filtrar Status)
    //TODO Verificar memory leak  da abertura de menu
    const { id: user_final_id } = this.storage.getLocalUser().user;
    this.transacaoService.countTransacoes({
      user_final_id,
      status_transacao_cod: StatusTransacaoEnum.PENDENTE
    })
      .pipe(takeUntil(this.obs))
      .subscribe(data => {
        this.transacoesPendentes = data.count;
      });

  }

  ionDidClose(): void {
    this.obs.next();
    this.obs.complete();
  }

  toggleDetails(p) {
    if (p.showDetails) {
      p.showDetails = false;
      p.icon = 'ios-arrow-down';
    } else {
      p.showDetails = true;
      p.icon = 'ios-arrow-up';
    }
  }

  toggleThemes(event) {
    const { checked } = event.detail;
    if (checked) {
      document.body.setAttribute('color-theme', 'dark');
      this.themeService.switchTheme('bootstrap4-dark-yellow');
      this.preferencesService.setThemePreference('dark');
    } else {
      document.body.setAttribute('color-theme', 'light');
      this.preferencesService.setThemePreference('light');
      this.themeService.switchTheme('saga-blue');
    }
  }

  logout() {
    this.auth.logout();
  }

  sincronizar() {
    setupWebSockets(true);
  }

  private async presentTransacaoRequest() {
    this.transacaoSocketService.listenNewTransacao()
      .subscribe(async data => {
        this.blockUI = true;
        const currentMenu = await this.menu.isEnabled();
        this.menu.enable(true);
        this.menu.open();

        const { transacao } = data as any;

        this.transacaoService.setTransacaoPendente([transacao]);

        this.ref = this.dialogService.open(NovaTransacaoModalComponent, {
          header: 'Nova transação requisitada',
          width: '70%',
          contentStyle: { "max-height": "500px", "overflow": "auto" },
          baseZIndex: 999999999999999999999,
          closable: false,

        });

        this.ref.onClose.subscribe(() => {
          this.blockUI = false;
          this.menu.enable(currentMenu);
        })
      });
  }

  private async showModal() {
    this.transacaoSocketService.listenNewTransacao()
      .subscribe(async data => {

        const { transacao } = data as any;
        this.transacaoService.setTransacaoPendente([transacao]);

        const modal = await this.modal.create({
          component: NovaTransacaoModalComponent,
          backdropDismiss: false,
          cssClass: 'modal-size-transacao-request',
          
        });


        return await modal.present();
      });
  }

  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }
}
