/* eslint-disable max-len */
import { NgModule } from '@angular/core';
import { NoPreloading, PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthGuardGuard } from './guards/auth-guard.guard';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: 'login',
    loadChildren: () => import('./pages/login/login.module').then(m => m.LoginPageModule)
  },
  {
    path: 'password-reset',
    loadChildren: () => import('./pages/password-reset/password-reset.module').then(m => m.PasswordResetPageModule),
  },
  {
    path: 'home',
    loadChildren: () => import('./pages/home/home.module').then(m => m.HomePageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'perfil',
    loadChildren: () => import('./pages/perfil/perfil.module').then(m => m.PerfilPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'administracao-financeira',
    loadChildren: () => import('./pages/administracao-financeira/administracao-financeira.module').then(m => m.AdministracaoFinanceiraPageModule),
    canLoad: [AuthGuardGuard],
    canActivate: [AuthGuardGuard],
    data: {
      role: ['ADMIN']
    }
  },
  {
    path: 'administracao-recurso',
    loadChildren: () => import('./pages/administracao-recurso/administracao-recurso.module').then(m => m.AdministracaoRecursoPageModule),
    canLoad: [AuthGuardGuard],
    canActivate: [AuthGuardGuard],
    data: {
      role: ['ADMIN']
    }
  },
  {
    path: 'administracao-geral',
    loadChildren: () => import('./pages/administracao-geral/administracao-geral.module').then(m => m.AdministracaoGeralPageModule),
    canLoad: [AuthGuardGuard],
    canActivate: [AuthGuardGuard],
    data: {
      role: ['ADMIN']
    }
  },
  {
    path: 'transacoes',
    loadChildren: () => import('./pages/transacoes/transacoes.module').then(m => m.TransacoesPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-1',
    loadChildren: () => import('./pages/fluxo-um/fluxo-um.module').then(m => m.FluxoUmPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-2',
    loadChildren: () => import('./pages/fluxo-dois/fluxo-dois.module').then(m => m.FluxoDoisPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-3',
    loadChildren: () => import('./pages/fluxo-tres/fluxo-tres.module').then(m => m.FluxoTresPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-4',
    loadChildren: () => import('./pages/fluxo-quatro/fluxo-quatro.module').then(m => m.FluxoQuatroPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-5',
    loadChildren: () => import('./pages/fluxo-cinco/fluxo-cinco.module').then(m => m.FluxoCincoPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-6',
    loadChildren: () => import('./pages/fluxo-seis/fluxo-seis.module').then(m => m.FluxoSeisPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-7',
    loadChildren: () => import('./pages/fluxo-sete/fluxo-sete.module').then(m => m.FluxoSetePageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-8',
    loadChildren: () => import('./pages/fluxo-oito/fluxo-oito.module').then(m => m.FluxoOitoPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-9',
    loadChildren: () => import('./pages/fluxo-nove/fluxo-nove.module').then(m => m.FluxoNovePageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-10',
    loadChildren: () => import('./pages/fluxo-dez/fluxo-dez.module').then(m => m.FluxoDezPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'fluxo-11',
    loadChildren: () => import('./pages/fluxo-onze/fluxo-onze.module').then(m => m.FluxoOnzePageModule),
    canLoad: [AuthGuardGuard],
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
