import './SocketStart';
import { initSocket } from './SocketStart';
import { StorageService } from '../services/auth/storage.service';

const storage = new StorageService();

function setupWebSockets(force: boolean = false) {

    if (storage && storage.getLocalUser()) {
        const { id: user_id } = storage.getLocalUser()?.user;

        if ((force) || (user_id && !storage.getWs())) {
            initSocket(user_id);
            storage.turnWsOn();
        }
    }else{
        console.log(`Web Socket não inicializado por usuário não estar autenticado!`);
    }


}

export { setupWebSockets };