import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/config/api.config';
import CredenciaisDTO from '../../interfaces/Credencias.dto';
import LocalUserDTO from '../../interfaces/LocalUser.sto';
import { StorageService } from './storage.service';
import { PreferencesService } from '../preferences/preferences.service';
import { setupWebSockets } from '../../webSocket';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  constructor(
    private http: HttpClient,
    private storage: StorageService,
    private preferencesService: PreferencesService,
  ) { }

  authenticate(credenciais: CredenciaisDTO): Observable<LocalUserDTO> {
    return this.http.post<LocalUserDTO>(`${API_CONFIG.baseURL}/autenticacao`, credenciais, {
      responseType: 'json',
    });
  }

  refreshToken(): Observable<any> {
    try {
      const token = {
        token: this.storage.getLocalUser().refresh_token
      };
      
      return this.http.post<any>(
        `${API_CONFIG.baseURL}/autenticacao/refresh`,
        token,
        {
          responseType: 'json',
        }
      );
    } catch (e) {
    }
  }

  loginSucesso(logedUser: LocalUserDTO) {
    this.storage.setLocalUser(logedUser);
    setupWebSockets();
  }

  logout() {
    //this.preferencesService.setThemePreference('light');
    this.storage.setLocalUser(null);
  }
}
