import { Injectable } from '@angular/core';
import {
  HttpInterceptor,
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HTTP_INTERCEPTORS,
} from '@angular/common/http';
import { Observable } from 'rxjs';
import { API_CONFIG } from '../../../config/api.config';
import { StorageService } from '../services/auth/storage.service';
import { MessageService } from 'primeng/api';
import { Router } from '@angular/router';
import { Toast } from '../enumerations/toast.enum';

@Injectable({
  providedIn: 'root',
})
export class AuthInterceptor implements HttpInterceptor {

  constructor(
    private storage: StorageService,
    private toastService: MessageService,
    private router: Router,
  ) { }
    // terminar o refresh token
  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    this.toastService.clear();
    const localUser = this.storage.getLocalUser();
    const N = API_CONFIG.baseURL.length;
    const reqToApi = req.url.substring(0, N) === API_CONFIG.baseURL;

    if (localUser && reqToApi) {
      const authReq = req.clone({
        headers: req.headers.set('Authorization', 'Bearer ' + localUser.refresh_token),
      });
      return next.handle(authReq);
    } else {
      return next.handle(req);
    }
  }

  private checkToken() {
    this.presentToast(
      'warn',
      'Acesse novamente',
      'É preciso autenticar-se novamente',
      Toast.mediumDuration
    );

    this.router.navigateByUrl('login');
  }

  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }
}

export const authInterceptorProvider = {
  provide: HTTP_INTERCEPTORS,
  useClass: AuthInterceptor,
  multi: true,
};
