import { Injectable } from '@angular/core';
import {
  HttpInterceptor,
  HttpHandler,
  HttpRequest,
  HttpEvent,
  HTTP_INTERCEPTORS,
} from '@angular/common/http';
import { AlertController, MenuController } from '@ionic/angular';

import { Observable, throwError } from 'rxjs';
import { catchError, finalize, timeout } from 'rxjs/operators';

import { MessageService } from 'primeng/api';

import { StorageService } from '../services/auth/storage.service';
import { Toast } from '../enumerations/toast.enum';
import { LoaderService } from '../services/app-loader/loader.service';
import { API_CONFIG } from 'src/config/api.config';
import { AuthService } from '../services/auth/auth.service';
import { Router } from '@angular/router';

@Injectable()
export class ErrorInterceptor implements HttpInterceptor {
  private timeoutTiming = 10000;
  constructor(
    private alertContr: AlertController,
    private toastService: MessageService,
    private loaderService: LoaderService,
    private authService: AuthService,
    private router: Router,
    private menu: MenuController,
  ) { }

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    this.loaderService.isLoading.next(true);
    this.loadBlock(true);
    return (
      next
        .handle(req)
        //Pipe para Erros
        .pipe(
          catchError(async (response) => {
            const { statusText } = response;
            if (statusText === 'Unknown Error') {
              return throwError('Timeout');
            }
            const { status } = response;

            if (response.error instanceof Blob) {
              response.error = JSON.parse(await response.error.text());
            }

            const { message } = response.error;

            switch (Number(status)) {
              case 400:
                this.handle400(message);
                break;

              case 403:
                this.handle403(message);
                break;

              case 401:
                this.handle401(message);
                break;

              case 422:
                this.handle422(message);
                break;

              case 404:
                this.handle404(message);
                break;

              default:
                this.handleDefaultError(message, status);
            }

            return throwError(message);
          })
        )
        //Pipe para Timeout
        .pipe(
          timeout(this.timeoutTiming),
          catchError((response) => {
            if (response !== 'Timeout') {
              return;
            }
            const e = `Tempo de espera excedido: ${API_CONFIG.baseURL}`;
            this.handle408(e);
            return throwError(e);
          })
        )
        //Pipe para loading de requisições
        .pipe(
          finalize(() => {
            this.loaderService.isLoading.next(false);
            this.loadBlock(false);
          })
        ) as any);
  }

  handle400(error: string) {
    this.presentToast(
      'warn',
      '400 - Requisição com problema',
      error,
      Toast.mediumDuration
    );
  }

  handle401(error: string) {
    if (error == 'Invalid token' || error == 'Token is missing') {
      this.presentToast(
        'warn',
        'Acesse novamente',
        'É preciso autenticar-se novamente',
        Toast.mediumDuration
      );
      this.menu.close();
      this.authService.logout();
      this.router.navigateByUrl('login');
    } else {
      this.presentToast(
        'warn',
        '401 - Não autorizado',
        error,
        Toast.mediumDuration
      );
    }
  }

  handle403(error: string) {
    this.presentToast('warn', '403 - Proibido', error, Toast.mediumDuration);
    //this.storage.setLocalUser(null);
  }

  handle404(error: string) {
    this.presentToast(
      'warn',
      '404 - Não encontrado',
      error,
      Toast.mediumDuration
    );
  }

  handle408(error: string) {
    this.presentToast(
      'error',
      '408 - Request Timeout',
      error,
      Toast.longDuration
    );
  }

  async handle422(error: string) {
    this.presentToast(
      'error',
      '422 - Não processável',
      error,
      Toast.longDuration
    );
  }

  handleDefaultError(message: string, status = 500) {
    this.presentToast('error', status.toString(), message, Toast.longDuration);
  }

  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }

  private async createAlert(error) {
    let titulo = error.error;
    let message = error.msg;

    if (!titulo) {
      titulo = 'Falha';
    }

    if (!message) {
      message = error.message;
    }

    const alert = await this.alertContr.create({
      header: error.status + ': ' + titulo,
      message,
      backdropDismiss: false,
      buttons: [
        {
          text: 'OK',
        },
      ],
    });

    await alert.present();
  }

  private loadBlock(isBlock = false): void {
    const value = isBlock ? 'none' : 'all';
    document.documentElement.style.setProperty('--load-block', value);
  }
}

export const errorInterceptorProvider = {
  provide: HTTP_INTERCEPTORS,
  useClass: ErrorInterceptor,
  multi: true,
};
