import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'app-justificar-rejeicao-modal',
  templateUrl: './justificar-rejeicao-modal.component.html',
  styleUrls: ['./justificar-rejeicao-modal.component.scss'],
})
export class JustificarRejeicaoModalComponent implements OnInit {

  @Input()
  isDialogVisible = false;

  @Output()
  notifyParent: EventEmitter<any> = new EventEmitter();

  form: FormGroup;

  erroMsg = 'Entre com seis caracteres numéricos';


  constructor(
    private formBuilder: FormBuilder,
  ) {

    this.form = this.formBuilder.group({
      justificativa: [, [Validators.required, Validators.minLength(5)]],
    });
  }

  ngOnInit() { }

  save() {
    if (this.saveButtonControl()) {
      return;
    }
    const justificativa = this.form.get('justificativa').value;
    this.form.reset();

    this.notifyParent.emit({
      justificativa,
      hasToClose: true,
    });
  }


  isInputError(inputName: string): boolean {
    const resp = this.form.controls[inputName].dirty && this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  saveButtonControl(): boolean {
    if (this.form.invalid) {
      return true;
    }

    return false;
  }

  cancel(event) {
    const { className } = event.target;
    if (className.includes('p-dialog-header-close')) {
      this.notifyParent.emit({
        justificativa: undefined,
        hasToClose: true,
      });
    }
  }

  close() {
    this.notifyParent.emit({
      justificativa: undefined,
      hasToClose: true,
    });
  }

}
