import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/config/api.config';
import { ChangeStatusTransacaoDTO } from '../../interfaces/transacoes/ChangeStatusTransacaoDTO';
import { CountTransacaoResponseDTO } from '../../interfaces/transacoes/CountTransacaoResponse';
import { AssociateAnexoResponseDTO, AssociateTransacaoToAnexoDTO, CreateResumoOperadoraDTO, CreateResumoOperadoraResponseDTO, CreateTransacaoDTO, UploadAnexoResponseDTO } from '../../interfaces/transacoes/CreateTransacaoDTO';
import { FiltroCountTransacaoDTO } from '../../interfaces/transacoes/FiltroCountTransacao';
import { FiltroTransacao } from '../../interfaces/transacoes/FiltroTransacao';
import { TipoTransacaoDTO } from '../../interfaces/transacoes/tipo/TipoTransacaoDTO';
import { TransacaoDTO } from '../../interfaces/transacoes/TransacaoDTO';

@Injectable({
  providedIn: 'root'
})
export class TransacaoService {

  private transacaoPendente: TransacaoDTO[] | undefined | null;

  constructor(
    private http: HttpClient,
  ) { }

  create(transacao: CreateTransacaoDTO): Observable<any> {
    return this.http.post<any>(`${API_CONFIG.baseURL}/transacao`, transacao, {
      responseType: 'json',
    });
  }

  changeStatus({ status, transacao_id, justificativa }: ChangeStatusTransacaoDTO): Observable<any> {
    const request = {
      status_transacao_cod: status,
      justificativa,
    }

    return this.http.patch<any>(`${API_CONFIG.baseURL}/transacao/${transacao_id}`, request, {
      responseType: 'json',
    });
  }

  update(transacao: CreateTransacaoDTO): Observable<any> {
    return this.http.put<any>(`${API_CONFIG.baseURL}/transacao`, transacao, {
      responseType: 'json',
    });
  }

  uploadAnexo(anexo: File, transacao_id: string): Observable<any> {
    const formData = new FormData();

    formData.append('file', anexo);

    return this.http.patch<any>(`${API_CONFIG.baseURL}/transacao/file/${transacao_id}`, formData);
  }

  list({ loterica_id, tipo_transacao_id, status_transacao_cod, user_final_id, user_inicial_id }: FiltroTransacao): Observable<TransacaoDTO[]> {
    console.log(status_transacao_cod);
    let statusCod: any = '';
    if (Number(status_transacao_cod) >= 0) {
      statusCod = status_transacao_cod;
    }
    console.log(statusCod);

    const params = new HttpParams()
      .set("loterica_id", loterica_id)
      .set("tipo_transacao_id", tipo_transacao_id ? tipo_transacao_id : '')
      .set("status_transacao_cod", statusCod)
      .set("user_final_id", user_final_id ? user_final_id : '')
      .set("user_inicial_id", user_inicial_id ? user_inicial_id : '');

    return this.http.get<TransacaoDTO[]>(`${API_CONFIG.baseURL}/transacao`, {
      responseType: 'json',
      params,
    });
  }

  countTransacoes({ status_transacao_cod, user_final_id }: FiltroCountTransacaoDTO): Observable<CountTransacaoResponseDTO> {

    let statusCod: any = '';
    if (status_transacao_cod >= 0) {
      statusCod = status_transacao_cod;
    }

    const params = new HttpParams()
      .set("status_transacao_cod", statusCod)
      .set("user_final_id", user_final_id ? user_final_id : '')

    return this.http.get<CountTransacaoResponseDTO>(`${API_CONFIG.baseURL}/transacao/count`, {
      responseType: 'json',
      params,
    });
  }

  listTipoTransacao(): Observable<TipoTransacaoDTO[]> {
    return this.http.get<TipoTransacaoDTO[]>(`${API_CONFIG.baseURL}/transacao/tipo`, {
      responseType: 'json',
    });
  }

  createResumoOperadora(transacao: CreateResumoOperadoraDTO): Observable<CreateResumoOperadoraResponseDTO> {
    return this.http.post<CreateResumoOperadoraResponseDTO>(`${API_CONFIG.baseURL}/transacao/resumo`, transacao, {
      responseType: 'json',
    });
  }

  insertAnexo(anexo: File): Observable<UploadAnexoResponseDTO> {
    const formData = new FormData();

    formData.append('file', anexo);

    return this.http.post<UploadAnexoResponseDTO>(`${API_CONFIG.baseURL}/transacao/resumo/file`, formData);
  }

  associateAnexoTransacoes({ file_id, transacoes_id }: AssociateTransacaoToAnexoDTO): Observable<AssociateAnexoResponseDTO[]> {
    return this.http.patch<AssociateAnexoResponseDTO[]>(`${API_CONFIG.baseURL}/transacao/resumo/${file_id}`, { transacoes_id }, {
      responseType: 'json',
    });
  }

  getTransacaoPendente() {
    return this.transacaoPendente;
  }

  setTransacaoPendente(transacaoPendente: TransacaoDTO[] | undefined | null) {
    this.transacaoPendente = transacaoPendente;
  }

}
